<template>
  <scroll-view @scrolltolower="handleToLower" class="album_view" scroll-y v-if="album.length>0">
    <!-- 
      轮播图 swiper 属性
      1.自动轮播 autoplay
      2.指示器 indicator-dots
      3.衔接轮播 circular
      4.swiper 默认的高度 150px
      5.image 默认宽度 320px => 基本样式中重置了 100%
        默认高度 240px
      6.计算图片的宽高比
      7.把图片的比例也写到swiper标签样式
     -->
    <!-- 轮播图 开始 -->
      <view class="album_swiper">
        <swiper
          autoplay
          indicator-dots
          circular
        >
          <swiper-item
            v-for="item in banner"
            :key="item.id"
          >
            <image :src="item.thumb"></image>
          </swiper-item>
        </swiper>
      </view>
    <!-- 轮播图 结束 -->

    <!-- 列表 开始 -->
      <view class="album_list">
        <navigator class="album_item"
          v-for="item in album"
          :key="item.id"
          :url="`/pages/album/index?id=${item.id}`"
          >
          <view class="album_img">
            <image mode="aspectFill" :src="item.cover"></image>
          </view>
          <view class="album_info">
            <view class="album_name">{{item.name}}</view>
            <view class="album_desc">{{item.desc}}</view>
            <view class="album_btn">
              <view class="album_attention">关注</view>
            </view>
          </view>
        </navigator>
      </view>
    <!-- 列表 结束 -->
  </scroll-view>
</template>

<script>
export default {
  data() {
    return {
      params: {
        limit:30,
        order:"new",
        skip:0
      },
      // 轮播图数据
      banner:[],
      // 列表数组
      album:[],
      // 判断是否还有数据
      hasMore:true
    }
  },
  mounted() {
    // 修改页面标题
    uni.setNavigationBarTitle({title:"专辑"})
    // 调用接口
    this.getList()
  },
  methods: {
    // 获取接口数据
    getList(){
      this.request({
        url:"http://service.picasso.adesk.com/v1/wallpaper/album",
        data:this.params,
      })
      .then(res=>{
        if(this.banner.length===0){
          this.banner = res.res.banner;
        }
        if(res.res.album.length===0){
          this.hasMore=false;
          return
        }
         this.album = [...this.album,...res.res.album]
      })
       
    },

    // 下拉加载数据
    handleToLower(){
      if(this.hasMore){
        this.params.skip +=this.params.limit
        this.getList() 
      }else{
        uni.showToast({
          title: '已全部加载',
          icon: 'none',
        });
      }
      
    }
  }
}
</script>

<style lang="scss" scoped>
  .album_view {
    height: calc( 100vh - 36px );
  }

  .album_swiper {
    swiper {
      height: calc( 750rpx / 2.3 );
      image {
        height: 100%;
      }
    }
  }

  .album_list {
    padding: 10rpx;
    .album_item {
      padding: 10rpx 0;
      display: flex;
      border-bottom: 1px solid #ccc;
      .album_img{
        flex: 1;
        padding: 10rpx;
        image {
          width: 200rpx;
          height: 200rpx;
        }
      }
      .album_info {
        flex: 2;
        padding: 0 10rpx;
        overflow: hidden;
        .album_name {
          font-size: 30rpx;
          color: #000;
          padding: 10rpx 0;
        }

        .album_desc {
          padding: 10rpx 0;
          font-size: 24rpx;

          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
        }

        .album_btn {
          padding: 10rpx;
          display: flex;
          justify-content: flex-end;
          .album_attention {
            color: $color;
            border: 1rpx solid $color;
            padding: 10rpx;
          }
        }

      
      }
    }
  }
</style>
